*,
*::before,
*::after {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}
body {
  font-family:
    "Inter",
    -apple-system,
    sans-serif;
  min-height: 100vh;
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  background-color: #cbd5c0;
}
.page-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 20px;
}

/* 2. Layout Container */
.app-container {
  background-color: #ffffff;
  width: 100%;
  max-width: 1600px;
  height: 90vh;
  display: grid;
  grid-template-columns: 280px 1fr;
  gap: 20px;
  padding: 20px;
  border-radius: 30px;
  box-shadow: 0 10px 40px rgba(0, 0, 0, 0.08);
  overflow: hidden;
  min-width: 900px;
}

/* 3. Sidebar Styling */
.sidebar {
  background-color: #f9f9f9;
  padding: 1.5rem;
  display: flex;
  flex-direction: column;
  border-radius: 20px;
  overflow-y: auto;
}
.sidebar h3 {
  font-size: 0.8rem;
  text-transform: uppercase;
  letter-spacing: 1px;
  color: #888;
  margin-bottom: 1rem;
  margin-top: 1.5rem;
}
.sidebar-item {
  display: flex;
  align-items: center;
  padding: 10px 12px;
  cursor: pointer;
  border-radius: 10px;
  font-weight: 500;
  color: #444;
  transition: all 0.2s ease;
  margin-bottom: 4px;
}
.sidebar-item:hover {
  background-color: #efefef;
}
.sidebar-item span {
  width: 16px;
  height: 16px;
  border-radius: 5px;
  margin-right: 12px;
  flex-shrink: 0;
}
.delete-tag-btn {
  margin-left: auto;
  background: none;
  border: none;
  color: #ff5f5f;
  font-size: 1.2rem;
  cursor: pointer;
  opacity: 0;
  transition: opacity 0.2s;
  line-height: 1;
}
.sidebar-item:hover .delete-tag-btn {
  opacity: 1;
}
.add-list-btn {
  margin-top: 10px;
  color: #666;
  font-weight: 600;
  border-top: 1px solid #eee;
  padding-top: 15px !important;
}

/* 4. Sticky Wall Grid */
.sticky-wall {
  padding: 20px;
  overflow-y: auto;
}
h1 {
  font-size: 2.5rem;
  font-weight: 800;
  margin-bottom: 1.5rem;
  color: #222;
}
.grid-container {
  padding: 10px;
}
#todo-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));
  gap: 25px;
}

/* 5. Sticky Note Cards */
.todo-card {
  position: relative;
  aspect-ratio: 1/1;
  padding: 24px;
  border-radius: 10px;
  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.04);
  display: flex;
  flex-direction: column;
  transition: transform 0.25s cubic-bezier(0.175, 0.885, 0.32, 1.275);
  border: 1px solid rgba(0, 0, 0, 0.05);
  overflow: hidden;
}
.card-delete-btn {
  position: absolute;
  top: 10px;
  right: 10px;
  background: rgba(0, 0, 0, 0.05);
  border: none;
  border-radius: 50%;
  width: 24px;
  height: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  font-size: 1.2rem;
  color: #444;
  opacity: 0;
  transition: all 0.2s ease;
  z-index: 10;
}
.todo-card:hover .card-delete-btn {
  opacity: 1;
}
.card-delete-btn:hover {
  background: #ff5f5f;
  color: white;
}
.todo-card:hover {
  transform: translateY(-8px);
}
.card-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  margin-bottom: 12px;
  padding-right: 20px;
}
.todo-card h3 {
  font-size: 1.3rem;
  font-weight: 700;
  line-height: 1.2;
  word-break: break-word;
}
.tag-pill {
  background: rgba(255, 255, 255, 0.5);
  padding: 4px 10px;
  border-radius: 8px;
  font-size: 0.7rem;
  font-weight: 700;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}
.card-desc {
  font-size: 0.95rem;
  color: #444;
  margin-bottom: 15px;
  flex-shrink: 0;
}

/* 6. Checklist inside Card */
.card-checklist {
  list-style: none;
  overflow-y: auto;
  flex-grow: 1;
  padding-right: 5px;
}
.card-checklist li {
  cursor: pointer;
  padding: 6px 0 6px 30px;
  position: relative;
  font-size: 0.9rem;
}
.card-checklist li::before {
  content: "○";
  position: absolute;
  left: 0;
  font-size: 1.2rem;
  color: rgba(0, 0, 0, 0.3);
}
.card-checklist li.completed {
  text-decoration: line-through;
  opacity: 0.5;
}
.card-checklist li.completed::before {
  content: "●";
  color: #222;
}
.card-checklist::-webkit-scrollbar {
  width: 4px;
}
.card-checklist::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.1);
  border-radius: 10px;
}
.card-checklist::-webkit-scrollbar-track {
  background: transparent;
}

/* 7. Add Button Card */
.add-card {
  background-color: #f0f0f0;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 3rem;
  color: #999;
}
.add-card:hover {
  background-color: #e8e8e8;
  border-color: #bbb;
}

/* 8. Modal */
.modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0, 0, 0, 0.3);
  backdrop-filter: blur(8px);
  display: flex;
  align-items: center;
  justify-content: center;
  z-index: 1000;
}
.modal-content {
  background: white;
  padding: 2.5rem;
  border-radius: 25px;
  width: 100%;
  max-width: 450px;
  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.15);
}
.modal-content input,
.modal-content textarea,
.modal-content select {
  width: 100%;
  padding: 14px;
  margin-bottom: 15px;
  margin-top: 10px;
  border: 1px solid #eee;
  background-color: #f9f9f9;
  border-radius: 12px;
  font-size: 1rem;
}
.modal-content select {
  margin-bottom: 0;
}
.modal-content textarea {
  resize: none;
}
.modal-actions {
  display: flex;
  gap: 12px;
  margin-top: 10px;
}
.modal-actions button {
  flex: 1;
  padding: 14px;
  border-radius: 12px;
  border: none;
  cursor: pointer;
  font-weight: 700;
  transition: opacity 0.2s;
}
button[type="submit"] {
  background: #222;
  color: white;
}
#close-modal {
  background: #f0f0f0;
}
.hidden {
  display: none;
}

.priority-stripe {
  height: 8px;
  width: 60px;
  position: absolute;
  top: -4px;
  left: 50%;
  transform: translateX(-50%);
  border-radius: 2px;
  opacity: 0.8;
}
.priority-stripe.high {
  background-color: #ff6b6b;
  box-shadow: 0 2px 4px rgba(255, 107, 107, 0.3);
}
.priority-stripe.medium {
  background-color: #ffd93d;
}
.priority-stripe.low {
  background-color: #6bc1ff;
}
.card-footer {
  margin-top: auto;
  text-align: right;
  font-size: 0.75rem;
  font-style: italic;
  opacity: 0.6;
}
.todo-card {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 180px;
}
.projects-section {
  display: flex;
  flex-direction: column;
  height: 100%;
}
.tags-scroll-container {
  max-height: 280px;
  overflow-y: auto;
  overflow-x: hidden;
  margin-bottom: 10px;
  padding-right: 8px;
}
.tags-scroll-container::-webkit-scrollbar {
  width: 5px;
}
.tags-scroll-container::-webkit-scrollbar-thumb {
  background: #e0e0e0;
  border-radius: 10px;
}
.tag-search-bar {
  width: 90%;
  padding: 8px;
  margin: 10px 0;
  border: 1px solid #eee;
  border-radius: 8px;
  background-color: #f9f9f9;
  font-size: 0.85rem;
  outline: none;
}
.tag-search-bar:focus {
  border-color: #d0f4de;
  background-color: #fff;
}

.sticky-wall-header {
  display: flex;
  align-items: center;
  justify-content: space-between; 
  margin-bottom: 2rem;
  gap: 20px;
}
#project-search {
  padding: 10px 15px;
  border-radius: 20px;
  border: 1px solid #ddd;
  width: 300px;
  outline: none;
  font-family: inherit;
  transition: all 0.3s ease;
  background-color: #fcfcfc;
}
#project-search:focus {
  width: 350px; 
  border-color: #a9def9;
  box-shadow: 0 2px 8px rgba(0,0,0,0.05);
}

.card-edit-btn {
  position: absolute;
  top: 35px;
  right: 12px; 
  background: none;
  border: none;
  cursor: pointer;
  font-size: 1.1rem;
  color: #444;
  opacity: 0;
  transition: all 0.2s ease;
}
.todo-card:hover .card-edit-btn{
  opacity: 0.5;
}

.card-edit-btn:hover {
  opacity: 1 !important;
  transform: scale(1.1);
}`, "",{"version":3,"sources":["webpack://./src/styles.css"],"names":[],"mappings":"AAAA,mCAAmC;AACnC;;;EAGE,sBAAsB;EACtB,UAAU;EACV,SAAS;AACX;AACA;EACE;;;cAGY;EACZ,iBAAiB;EACjB,gBAAgB;EAChB,mCAAmC;EACnC,yBAAyB;AAC3B;AACA;EACE,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,iBAAiB;EACjB,aAAa;AACf;;AAEA,wBAAwB;AACxB;EACE,yBAAyB;EACzB,WAAW;EACX,iBAAiB;EACjB,YAAY;EACZ,aAAa;EACb,gCAAgC;EAChC,SAAS;EACT,aAAa;EACb,mBAAmB;EACnB,2CAA2C;EAC3C,gBAAgB;EAChB,gBAAgB;AAClB;;AAEA,uBAAuB;AACvB;EACE,yBAAyB;EACzB,eAAe;EACf,aAAa;EACb,sBAAsB;EACtB,mBAAmB;EACnB,gBAAgB;AAClB;AACA;EACE,iBAAiB;EACjB,yBAAyB;EACzB,mBAAmB;EACnB,WAAW;EACX,mBAAmB;EACnB,kBAAkB;AACpB;AACA;EACE,aAAa;EACb,mBAAmB;EACnB,kBAAkB;EAClB,eAAe;EACf,mBAAmB;EACnB,gBAAgB;EAChB,WAAW;EACX,yBAAyB;EACzB,kBAAkB;AACpB;AACA;EACE,yBAAyB;AAC3B;AACA;EACE,WAAW;EACX,YAAY;EACZ,kBAAkB;EAClB,kBAAkB;EAClB,cAAc;AAChB;AACA;EACE,iBAAiB;EACjB,gBAAgB;EAChB,YAAY;EACZ,cAAc;EACd,iBAAiB;EACjB,eAAe;EACf,UAAU;EACV,wBAAwB;EACxB,cAAc;AAChB;AACA;EACE,UAAU;AACZ;AACA;EACE,gBAAgB;EAChB,WAAW;EACX,gBAAgB;EAChB,0BAA0B;EAC1B,4BAA4B;AAC9B;;AAEA,wBAAwB;AACxB;EACE,aAAa;EACb,gBAAgB;AAClB;AACA;EACE,iBAAiB;EACjB,gBAAgB;EAChB,qBAAqB;EACrB,WAAW;AACb;AACA;EACE,aAAa;AACf;AACA;EACE,aAAa;EACb,4DAA4D;EAC5D,SAAS;AACX;;AAEA,yBAAyB;AACzB;EACE,kBAAkB;EAClB,iBAAiB;EACjB,aAAa;EACb,mBAAmB;EACnB,0CAA0C;EAC1C,aAAa;EACb,sBAAsB;EACtB,mEAAmE;EACnE,qCAAqC;EACrC,gBAAgB;AAClB;AACA;EACE,kBAAkB;EAClB,SAAS;EACT,WAAW;EACX,+BAA+B;EAC/B,YAAY;EACZ,kBAAkB;EAClB,WAAW;EACX,YAAY;EACZ,aAAa;EACb,mBAAmB;EACnB,uBAAuB;EACvB,eAAe;EACf,iBAAiB;EACjB,WAAW;EACX,UAAU;EACV,yBAAyB;EACzB,WAAW;AACb;AACA;EACE,UAAU;AACZ;AACA;EACE,mBAAmB;EACnB,YAAY;AACd;AACA;EACE,2BAA2B;AAC7B;AACA;EACE,aAAa;EACb,8BAA8B;EAC9B,uBAAuB;EACvB,mBAAmB;EACnB,mBAAmB;AACrB;AACA;EACE,iBAAiB;EACjB,gBAAgB;EAChB,gBAAgB;EAChB,sBAAsB;AACxB;AACA;EACE,oCAAoC;EACpC,iBAAiB;EACjB,kBAAkB;EAClB,iBAAiB;EACjB,gBAAgB;EAChB,yBAAyB;EACzB,qBAAqB;AACvB;AACA;EACE,kBAAkB;EAClB,WAAW;EACX,mBAAmB;EACnB,cAAc;AAChB;;AAEA,6BAA6B;AAC7B;EACE,gBAAgB;EAChB,gBAAgB;EAChB,YAAY;EACZ,kBAAkB;AAC