rflow-y: auto;\n}\nh1 {\n  font-size: 2.5rem;\n  font-weight: 800;\n  margin-bottom: 1.5rem;\n  color: #222;\n}\n.grid-container {\n  padding: 10px;\n}\n#todo-grid {\n  display: grid;\n  grid-template-columns: repeat(auto-fill, minmax(280px, 1fr));\n  gap: 25px;\n}\n\n/* 5. Sticky Note Cards */\n.todo-card {\n  position: relative;\n  aspect-ratio: 1/1;\n  padding: 24px;\n  border-radius: 10px;\n  box-shadow: 0 4px 15px rgba(0, 0, 0, 0.04);\n  display: flex;\n  flex-direction: column;\n  transition: transform 0.25s cubic-bezier(0.175, 0.885, 0.32, 1.275);\n  border: 1px solid rgba(0, 0, 0, 0.05);\n  overflow: hidden;\n}\n.card-delete-btn {\n  position: absolute;\n  top: 10px;\n  right: 10px;\n  background: rgba(0, 0, 0, 0.05);\n  border: none;\n  border-radius: 50%;\n  width: 24px;\n  height: 24px;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  cursor: pointer;\n  font-size: 1.2rem;\n  color: #444;\n  opacity: 0;\n  transition: all 0.2s ease;\n  z-index: 10;\n}\n.todo-card:hover .card-delete-btn {\n  opacity: 1;\n}\n.card-delete-btn:hover {\n  background: #ff5f5f;\n  color: white;\n}\n.todo-card:hover {\n  transform: translateY(-8px);\n}\n.card-header {\n  display: flex;\n  justify-content: space-between;\n  align-items: flex-start;\n  margin-bottom: 12px;\n  padding-right: 20px;\n}\n.todo-card h3 {\n  font-size: 1.3rem;\n  font-weight: 700;\n  line-height: 1.2;\n  word-break: break-word;\n}\n.tag-pill {\n  background: rgba(255, 255, 255, 0.5);\n  padding: 4px 10px;\n  border-radius: 8px;\n  font-size: 0.7rem;\n  font-weight: 700;\n  text-transform: uppercase;\n  letter-spacing: 0.5px;\n}\n.card-desc {\n  font-size: 0.95rem;\n  color: #444;\n  margin-bottom: 15px;\n  flex-shrink: 0;\n}\n\n/* 6. Checklist inside Card */\n.card-checklist {\n  list-style: none;\n  overflow-y: auto;\n  flex-grow: 1;\n  padding-right: 5px;\n}\n.card-checklist li {\n  cursor: pointer;\n  padding: 6px 0 6px 30px;\n  position: relative;\n  font-size: 0.9rem;\n}\n.card-checklist li::before {\n  content: \"○\";\n  position: absolute;\n  left: 0;\n  font-size: 1.2rem;\n  color: rgba(0, 0, 0, 0.3);\n}\n.card-checklist li.completed {\n  text-decoration: line-through;\n  opacity: 0.5;\n}\n.card-checklist li.completed::before {\n  content: \"●\";\n  color: #222;\n}\n.card-checklist::-webkit-scrollbar {\n  width: 4px;\n}\n.card-checklist::-webkit-scrollbar-thumb {\n  background: rgba(0, 0, 0, 0.1);\n  border-radius: 10px;\n}\n.card-checklist::-webkit-scrollbar-track {\n  background: transparent;\n}\n\n/* 7. Add Button Card */\n.add-card {\n  background-color: #f0f0f0;\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  font-size: 3rem;\n  color: #999;\n}\n.add-card:hover {\n  background-color: #e8e8e8;\n  border-color: #bbb;\n}\n\n/* 8. Modal */\n.modal-overlay {\n  position: fixed;\n  inset: 0;\n  background: rgba(0, 0, 0, 0.3);\n  backdrop-filter: blur(8px);\n  display: flex;\n  align-items: center;\n  justify-content: center;\n  z-index: 1000;\n}\n.modal-content {\n  background: white;\n  padding: 2.5rem;\n  border-radius: 25px;\n  width: 100%;\n  max-width: 450px;\n  box-shadow: 0 25px 50px rgba(0, 0, 0, 0.15);\n}\n.modal-content input,\n.modal-content textarea,\n.modal-content select {\n  width: 100%;\n  padding: 14px;\n  margin-bottom: 15px;\n  margin-top: 10px;\n  border: 1px solid #eee;\n  background-color: #f9f9f9;\n  border-radius: 12px;\n  font-size: 1rem;\n}\n.modal-content select {\n  margin-bottom: 0;\n}\n.modal-content textarea {\n  resize: none;\n}\n.modal-actions {\n  display: flex;\n  gap: 12px;\n  margin-top: 10px;\n}\n.modal-actions button {\n  flex: 1;\n  padding: 14px;\n  border-radius: 12px;\n  border: none;\n  cursor: pointer;\n  font-weight: 700;\n  transition: opacity 0.2s;\n}\nbutton[type=\"submit\"] {\n  background: #222;\n  color: white;\n}\n#close-modal {\n  background: #f0f0f0;\n}\n.hidden {\n  display: none;\n}\n\n.priority-stripe {\n  height: 8px;\n  width: 60px;\n  position: absolute;\n  top: -4px;\n  left: 50%;\n  transform: translateX(-50%);\n  border-radius: 2px;\n  opacity: 0.8;\n}\n.priority-stripe.high {\n  background-color: #ff6b6b;\n  box-shadow: 0 2px 4px rgba(255, 107, 107, 0.3);\n}\n.priority-stripe.medium {\n  background-color: #ffd93d;\n}\n.priority-stripe.low {\n  background-color: #6bc1ff;\n}\n.card-footer {\n  margin-top: auto;\n  text-align: right;\n  font-size: 0.75rem;\n  font-style: italic;\n  opacity: 0.6;\n}\n.todo-card {\n  position: relative;\n  display: flex;\n  flex-direction: column;\n  min-height: 180px;\n}\n.projects-section {\n  display: flex;\n  flex-direction: column;\n  height: 100%;\n}\n.tags-scroll-container {\n  max-height: 280px;\n  overflow-y: auto;\n  overflow-x: hidden;\n  margin-bottom: 10px;\n  padding-right: 8px;\n}\n.tags-scroll-container::-webkit-scrollbar {\n  width: 5px;\n}\n.tags-scroll-container::-webkit-scrollbar-thumb {\n  background: #e0e0e0;\n  border-radius: 10px;\n}\n.tag-search-bar {\n  width: 90%;\n  padding: 8px;\n  margin: 10px 0;\n  border: 1px solid #eee;\n  border-radius: 8px;\n  background-color: #f9f9f9;\n  font-size: 0.85rem;\n  outline: none;\n}\n.tag-search-bar:focus {\n  border-color: #d0f4de;\n  background-color: #fff;\n}\n\n.sticky-wall-header {\n  display: flex;\n  align-items: center;\n  justify-content: space-between; \n  margin-bottom: 2rem;\n  gap: 20px;\n}\n#project-search {\n  padding: 10px 15px;\n  border-radius: 20px;\n  border: 1px solid #ddd;\n  width: 300px;\n  outline: none;\n  font-family: inherit;\n  transition: all 0.3s ease;\n  background-color: #fcfcfc;\n}\n#project-search:focus {\n  width: 350px; \n  border-color: #a9def9;\n  box-shadow: 0 2px 8px rgba(0,0,0,0.05);\n}\n\n.card-edit-btn {\n  position: absolute;\n  top: 35px;\n  right: 12px; \n  background: none;\n  border: none;\n  cursor: pointer;\n  font-size: 1.1rem;\n  color: #444;\n  opacity: 0;\n  transition: all 0.2s ease;\n}\n.todo-card:hover .card-edit-btn{\n  opacity: 0.5;\n}\n\n.card-edit-btn:hover {\n  opacity: 1 !important;\n  transform: scale(1.1);\n}"],"sourceRoot":""}]);
